.bounding-box{
	
 position: absolute;
 box-shadow: 0 0 0 3px #149df2 inset;
 display: -webkit-flex;
 display: flex;
 -webkit-flex-wrap: wrap;
         flex-wrap: wrap;
 -webkit-justify-content: center;
         justify-content: center;
 cursor: pointer;

}
.Tilt{

	background:transparent;
}
.form{

   width: 700px;
   background:
    -webkit-radial-gradient(black 3px, transparent 4px),
    -webkit-radial-gradient(black 3px, transparent 4px),
    -webkit-linear-gradient(#fff 4px, transparent 0),
    -webkit-linear-gradient(45deg, transparent 74px, transparent 75px, #a4a4a4 75px, #a4a4a4 76px, transparent 77px, transparent 109px),
    -webkit-linear-gradient(135deg, transparent 75px, transparent 76px, #a4a4a4 76px, #a4a4a4 77px, transparent 78px, transparent 109px),
    #fff;
   background:
    radial-gradient(black 3px, transparent 4px),
    radial-gradient(black 3px, transparent 4px),
    linear-gradient(#fff 4px, transparent 0),
    linear-gradient(45deg, transparent 74px, transparent 75px, #a4a4a4 75px, #a4a4a4 76px, transparent 77px, transparent 109px),
    linear-gradient(-45deg, transparent 75px, transparent 76px, #a4a4a4 76px, #a4a4a4 77px, transparent 78px, transparent 109px),
    #fff;
    background-size: 109px 109px, 109px 109px,100% 6px, 109px 109px, 109px 109px;
    background-position: 54px 55px, 0px 0px, 0px 0px, 0px 0px, 0px 0px;

}
